<template>
  <div class="lock">
    <SearchEquip v-bind="currentEquip"/>
    <div class="shim">
      <div class="modal">
        <input v-model='currentEquip.name'></input>
        <input v-model='currentEquip.serialNumber'></input>
        <select v-model='currentEquip.location'>
          <option v-for='location in locations' v-bind:value='location'>{{location.name}}</option>
        </select>
        <button @click="updateEquip">Update equip</button>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios"
import SearchEquip from "./SearchEquip"

const API_URL = 'http://localhost:8088/api/equip';

  export default{
    components: {SearchEquip},
    props: {
      locations: Array,
      currentEquip: {}
    },
    name: 'SearchEquip',
    data() {
      return {
        equip: this.currentEquip
      }
    },
    methods: {
      updateEquip() {
        axios
          .post(API_URL + "/updateEquip/", this.currentEquip)
          .then(response => {
            this.currentEquip = response.data;
          });
      }
    },
    mounted() {

    }
  }

</script>
<style>
.lock {
    /* убираем скроллбары с основнового содержимого страницы */
    overflow: hidden;
}

/* ширма (полупрозрачное затенение под модальным окном) */
.shim {
    /* фиксируем, растягиваем на весь доступный экран */
    position: fixed;
    bottom: 0; left: 0; top: 0; right: 0;

    /* если в модальном окне много содержимого, показываем скроллбар */
    overflow: auto;

    /* однопальцевый скролл для iPad*/
    -webkit-overflow-scrolling: touch;
}

/* фикс для странностей в iPad */
.shim > * {
    -webkit-transform: translateZ(0px);
}

/* декоративная часть ширмы: делаем полупрозрачной и чёрной */
.shim {
    background: rgba(0,0,0,0.5);

    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#7F000000,endColorstr=#7F000000); /* IE6–IE8 */
    zoom: 1;
}
</style>



